<template>
  <div class="course-practise-response">
    <head-top :title="libraryInfo.name || '提交成员'">
      <span slot="right" v-if="libraryInfo.state === 2" @click="toStop">
        <van-icon class-prefix="iconfont icon-jieshu" color="#fff" class="head-right"></van-icon>
      </span>
    </head-top>

    <div class="sub_nav">
      <span>提交成员</span>
      <span>{{submitInfo.subNum}} / {{submitInfo.total}}</span>
    </div>
    <div class="sub_module">
      <span v-if="submitInfo.persons.length===0">暂无数据</span>
      <div class="flex-wrap-between" v-else>
        <div v-for="(sub, k) in submitInfo.persons" :key="k" class="student_card van-hairline--surround">
          <img :src="sub.imgSrc" class="col_4" alt=""/>
          <div>{{sub.studentName}}</div>
        </div>
        <div class="student_card" style="width: 19vw;" v-for="d in Math.abs(4 - submitInfo.persons.length % 4) % 4"
             :key="-d">
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { Dialog } from "vant";
import defaultImg from "../../../../assets/boy_img.png";

export default {
  name: "practiseResponse",
  data () {
    return {
      libraryInfo: this.$store.getters.practiseLibrary,
      submitInfo: {
        total: 0,
        subNum: 0,
        persons: []
      }
    };
  },
  created () {
    this.getSubmitPerson();
  },
  methods: {
    getSubmitPerson () {
      this.submitInfo = {
        total: 0,
        subNum: 0,
        persons: []
      };
      this.$gatewayHttp.classPractise.getSubmitPerson({
        cipID: this.libraryInfo.taskID
      }).then(res => {
        if (res.Result) {
          this.submitInfo.subNum = res["SubNum"] || 0;
          this.submitInfo.total = res["Total"] || 0;
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.submitInfo.persons.push({
                studentID: item["StudentID"],
                studentName: item["StudentName"],
                imgSrc: defaultImg
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    toStop () {
      Dialog.confirm({
        title: '提示',
        message: '确定结束答题？'
      }).then(() => {
        this.postStop();
      }).catch(() => {
      });
    },
    postStop () {
      this.$gatewayHttp.classPractise.setStopAnswer({
        cipID: this.libraryInfo.taskID,
        plID: this.libraryInfo.id
      }).then(res => {
        if (res.Result) {
          this.$toast("结束答题成功");
          this.$router.replace("/course/class-practise/list");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("结束答题失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-practise-response {
  min-height: 100%;
  background-color: #fff;
}
</style>
